<template>
  <div id="app">
    <div ref="map" class="benny"></div>
  </div>
</template>
 <script>
export default {
  name: 'app',
  data() {
    return {
    };
  },
  props: ['lat', 'lon', 'city'],
  mounted() {
    const map = new google.maps.Map(this.$refs.map, {
      zoom: 12,
      center: {
        lat: parseFloat(this.lat),
        lng: parseFloat(this.lon)
      }
    })

    const marker = new google.maps.Marker({
      position: {
        lat: parseFloat(this.lat),
        lng: parseFloat(this.lon)
        // icon:'bot.png'   可以多加屬性改變icon
      },
      map: map,
      title: this.city,
      // 可以自訂圖案
      // icon: 'xxx.png'
    });

    // 建立彈跳視窗
    const infowindow = new google.maps.InfoWindow({
      content: this.city
    });

    // 偵聽click事件
    marker.addListener('click', function () {
      infowindow.open(map, marker);
    });

  }
};
</script>
<style lang="scss" scoped>
.benny {
  margin-top: 10px;
  min-height: 250px;
  width: 100%; // max-height: 300px;
}
</style>


